* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  max-height: 100%;
}

a {
  text-decoration: none;
  color: #999;
}
a:hover {
  color: black;
}

ul {
  list-style: none;
}

.row {
  max-width: 1027px;
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 200px;
}
.logo {
  width: 150px;
}
#nav {
  display: flex;
}

#nav div {
  margin: 0 25px;
  font-size: 20px;
}

.wall {
  display: flex;
  height: 750px;
  justify-content: space-between;
}

.wall .left {
  width: 49%;
}

.wall .right {
  width: 49%;
  height: 100%;
}

.bottom {
  display: flex;
  justify-content: space-between;
}

.bottom .wrap {
  width: 30%;
  text-align: center;
}

.bottom .wrap img {
  margin-bottom: 20px;
}

.footer {
  margin-top: 150px;
  background-color: black;
  height: 70px;
  text-align: center;
  font-size: 25px;
  color: white;
  line-height: 70px;
}

.tab {
  display: flex;
  justify-content: space-between;
  text-align: center;
  height: 60px;
  margin: 30px 0;
  line-height: 60px;
}
section {
  height: 1000px;
}
section .tab div {
  width: 25%;
  background-color: rgba(197, 197, 197, 0.274)
}

section .tab div:hover {
  background-color: rgba(197, 197, 197, 0.671);
  cursor: pointer;
}

section ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

section ul li {
  width: 32.5%;
  margin-bottom: 5px;
}


section ul li img {
  width: 100%;
}